@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  --primaryColor: #af9a7d;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}

.btn-primary {
  display: inline-block;
  text-decoration: none;
  letter-spacing: 3px;
  letter-spacing: var(--mainSpacing);
  color: #222;
  color: var(--mainBlack);
  background: #af9a7d;
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid #af9a7d;
  border: 3px solid var(--primaryColor);
  transition: all 0.3s linear;
  transition: var(--mainTransition);
  text-transform: uppercase;
  cursor: pointer;
}
/* Hero */
.defaultHero,
.roomsHero {
  min-height: 100vh;
  background: url("https://images.unsplash.com/photo-1497032628192-86f99bcd76bc?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: left;
  
}
.roomsHero {
  background-image: url(/ritikportfolio/static/media/defaultB.7da94822.jpg);
  min-height: 60vh;
  
}
/* End of Hero */
/* Banner */
.banner-container{
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: space-evenly;
align-content: center;
}
.banner {
  display: inline-block;
  background: rgba(0, 0, 0, 0.911);
  color: #fff;
  color: var(--mainWhite);
  padding: 2rem 1rem;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: 3px;
  letter-spacing: var(--mainSpacing);
  height: 350px;
  border-radius: 25px;
  /* border: 2px solid darkgrey; */
  margin: 5px;
}
.banner h2 {
  font-size: 2.2rem;
}
.banner div {
  width: 30rem;
  height: 8px;
  
  margin: .5rem auto;
}
.banner-para {
  font-size: 1.5rem;
}


.t-special{
  font-weight: 500;
  color: #3afd09;
  
  
}

.banner-image{
  border-radius: 50%;
  width: 350px;
  padding: 10px;
 /* margin-right: 100px; */
 border: 20px solid rgb(10, 10, 10);
 transition: .5s;
}

.banner-image:hover {
  box-shadow: -1em 0 2em #00ff15,1em 0 2em #00ff15,0em 1em 2em #00ff15,0em -1em 2em #00ff15;

}

@media screen and (min-width: 1700px) {
.banner-image{
  width: 450px;
}
.banner {
  
  height: 450px;
  
}
.banner h2 {
  padding-top: 3rem;
  font-size: 5rem;
}
.banner div {
  width: 30rem;
  height: 18px;
  
  margin: 2rem auto;
}
.banner-para {
  font-size: 1.8rem;
}



}



@media screen and (max-width: 576px) {
  .banner-image{
    display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  }
  .banner-para{
    font-size: 1.2rem;
  }
  .banner div {
    width: 16rem;
    height: 20px;
    
    margin: 1.7rem auto;
  } 
}

@media screen and (max-width: 350px) {
.banner{
  width: 290px;
  height: 300px;
}

}

@media screen and (min-width: 576px) {
  .banner {
    padding: 2rem 3rem;
  }
  .banner h1 {
    font-size: 3rem;
  }
}

@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h1 {
    font-size: 4rem;
  }
}
@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h2 {
    font-size: 3rem;
  }
}
/* End of Banner */
/* Title */
.section-title {
  text-align: center;
  margin-bottom: 4rem;
}
.section-title h4 {
  font-size: 2rem;
  letter-spacing: 3px;
  letter-spacing: var(--mainSpacing);
  text-transform: capitalize;
  margin-bottom: 1rem;
}
.section-title div {
  width: 5rem;
  height: 5px;
  margin: 0 auto;
  background: #af9a7d;
  background: var(--primaryColor);
}
/* end of Title */

