.button{
    height: 37px;
    width: 80px;
}
.button button{
    width: 100%;
    height: 100%;
    border: 2px solid rgb(5, 248, 37);
    background: rgb(8, 218, 54);
    color: #fff;
    outline: none;
    font-size: medium;
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.button button:hover{
    color: #07f71b;
    background: none;
}
