.aboutMe-container{
    display: flex;
  align-items: center;
  justify-content: center;
  
}
.about-heading{
color: #1eff00;
margin-top: 20px;
margin-bottom: 30px;
text-align: center;
font-size: 50px;
}
.about-para {
    display: inline-block;
    background: linear-gradient(to bottom,#050608,#0c0b0c);
    padding: 4rem 0;    
    color: #a1c6eb;
    font-size: x-large;    
  }
  
.about-para > p {
  padding : 0  1rem;
  text-align: justify;
}
.education-heading{
  padding-top: 40px;
  padding-bottom: 10px;
  padding-left: 100px;
}

.education-content-container {
  display: flex;
  flex-direction: column;
  max-width: 900px;
  padding-left: 10px;  
}


.education-content{
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
}
.education-content > div {
  width: 550px;
}


.education-icons{
  font-size: 60px;
  color: #1eff00;
margin-left: 135px;
}

.education-details{
  display: flex;
  
}

.education-details > p {
  padding-left: 10px;
}

@media screen and (max-width: 576px) {
  .about-heading{
    font-size: 40px;
  }
  .education-content > div {
    width: 320px;
  }
  .education-icons{
    font-size: 30px;
    color: #1eff00;
 margin-left: 0px; 
padding-right: 15px;  
}
.about-para {
font-size: medium;
}  
.education-content{
  display: flex;
  justify-content: flex-start;
  
}
.education-heading{
    padding-left: 10px;
padding-bottom: 5px;
  } 
  .about-para > p {
    text-align: center;
  }
}

@media screen and (max-width: 350px){
  .education-content > div {
    width: 280px;
  } 
}